body {
  background-color: #94868b;
}

.hidden {
  display: none;
}

.checkCats {
  max-width: 300px;
  text-align: center;
  margin: auto;
  display: block;
  position: relative;
}

.loader {
  height: 12px;
  width: 80%;
  border: 1px solid #fff;
  border-radius: 10px;
  display: block;
  position: relative;
  overflow: hidden;
}

.loader:after {
  content: "";
  height: 100%;
  width: 0;
  background: #ff3d00;
  animation: prog .8s ease-in infinite;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes prog {
  to {
    width: 100%;
  }
}

div.polaroid {
  width: 50%;
  background-color: #fff;
  border-radius: 10px;
  margin: 15px auto auto;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .2), 0 6px 20px rgba(0, 0, 0, .19);
}

.imgCats {
  max-width: 80%;
  border: 1px;
  border-radius: 5px;
  margin: auto;
  transition-duration: 2s;
  display: block;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .2), 0 6px 20px rgba(0, 0, 0, .19);
}

.imgCats:hover, .imgCats:focus {
  transform: scale(1.8);
}

div.container {
  text-align: center;
  padding: 10px 20px;
}

